	module.exports = {"data":{"markdownRemark":{"html":"<h2>Abbr Tag</h2>\n<p>The <code>&#x3C;abbr></code> tag stands for ‘abbreviation’. It can take <code>title</code> attribute, to explain the abbreviation.</p>\n<p>Example usage:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>abbr</span> <span class=\"token attr-name\">title</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>Free Code Camp<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>FCC<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>abbr</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h4>More Information:</h4>\n<ul>\n<li><a href='https://developer.mozilla.org/en-US/docs/Web/HTML/Element/abbr' target='_blank' rel='nofollow'>The HTML &#x3C;abbr> element: MDN</a></li>\n<li><a href='https://developer.mozilla.org/en-US/docs/Learn/HTML/Introduction_to_HTML/Advanced_text_formatting#Abbreviations' target='_blank' rel='nofollow'>Advanced Text Formatting: Abbreviations</a></li>\n</ul>","fields":{"slug":"/html/elements/abbr-tag/"},"frontmatter":{"title":"Abbr Tag"}}},"pathContext":{"slug":"/html/elements/abbr-tag/"}}

